import React, { useEffect } from 'react';
import PDFViewer from './components/contents/PDFViewer';
import { getChromeVersion }from './utils/utilFunction';
import PDFViewerForN from './components/contents/PDFViewerForN';

function App() {
  useEffect(() => {

  }, [])

  const getPDFViwerComponents = () => {
    console.log(getChromeVersion());
    if (getChromeVersion() >= 92) {
      return <PDFViewer />
    } else {
      console.log('');
      return <PDFViewerForN />
    }
  }

  return (
    <>
      {getPDFViwerComponents()}
    </>

  );
}

export default App;
